.footer{
    position: relative;
    padding: 300px 0 45px;
    background: url('../images/backgrounds/footer-bg.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: top center;
    .background-shape{
        position: absolute;
        width: 100%;
        bottom: 0;
        z-index: -1;
    }
    .block{
        h2{
            margin-bottom: 35px;
        }
    }
    .footer-form{
        margin-bottom: 80px;
        .input-group{
            box-shadow: 0px 32px 44px 0px rgba(0, 0, 0, 0.2);
        }
    }
}
