.hero-area{
    background-image: url('../images/backgrounds/header-bg.png');
    background-size: cover;
    background-position: bottom center;
    margin-bottom: 200px;
}

.hero-header{
    margin-bottom: 70px;
    h1,p{
        color: $white;
    }
    p{
        font-size: 23px;
    }
}

.insta-count{
    background: $white;
    border-radius: 4px;
    box-shadow: 0px 32px 44px 0px rgba(0, 0, 0, 0.05);
    padding: 20px 55px 70px;
    ul.nav-tabs{
        display: flex;
        border-bottom: 2px solid $border-color;
        .nav-item{
            flex-basis: 50%;
            text-align: center;
            .nav-link{
                display: flex;
                align-items: center;
                justify-content: center;
                padding: 20px 0;
                border-radius: 0;
                h5{
                    margin-bottom: 0;
                    margin-left: 10px;
                    color: $text-color;
                    font-weight: 400;
                }
                .icon{
                    svg{
                        fill: $text-color;
                    }
                }
                &.active{
                    border-width: 0 0 2px 0;
                    border-color: $black;
                    border-style: solid;
                    transform: translateY(1px);
                    h5{
                        color: $text-color-dark;
                    }
                    .icon{
                        svg{
                            fill: $text-color-dark;
                        }
                    }
                }
                &:hover{
                    border-width: 0 0 2px 0;
                    border-color: $black;
                    border-style: solid;
                    transform: translateY(1px);
                }
            }
        }
    }
    .user-details{
        margin-top: 55px;
        margin-bottom: 70px;
        img{
            border-radius: 50%;
            margin-bottom: 10px;
        }
        .username{
            margin-bottom: 35px;
        }
    }
    .checkform{
        padding: 0 50px;
    }
}